
*, *:before, *:after {
  box-sizing: border-box;
}

.img-slider-frame {
  position: relative;
  width: clamp(100%, 35vw, 125%);   /* slider */

  height: 100%;            /* or whatever height you want */
  overflow: hidden;
  padding: 0px;
  border: 1px solid rgba(211, 211, 211, 0.397);            // visible border
  border-radius: 25px;
  align-items: center;
  align-content: center;
  align-self: center;
  justify-content: center;

  box-shadow: 0px 10px 50px -15px #d9dbdf;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;

}
.img-slider-frame:hover{
  box-shadow: 0px 20px 75px -10px #d9dbdf;
}


.img-slider-img {
  object-fit: cover;
  width: 100%;
  min-height: 0;
  min-width: 0;
  height: auto;
  display: block;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 15px;

  transition: translate 300ms ease-in-out;

}


.img-slider-btn {
  all: unset;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 1rem;
  cursor: pointer;
  border-radius: 15px;

  transition: background-color 200ms ease-in-out;

}

.img-slider-btn > * {
  stroke: white;
  fill: rgba(37, 37, 39, 0.59);
  width:  clamp(15px, 1.75vw, 35px);
  height: clamp(15px, 1.75vw, 35px);
  stroke-width: 0.05rem;

}

.img-slider-btn:hover,
.img-slider-btn:focus-visible
 {
  background-color: rgba(0, 0, 0, 0.2);


}

.img-slider-btn:focus {
  outline: none;


}

.img-slider-btn:hover > *,
.img-slider-btn:focus-visible > *{
  animation: squish 200ms ease-in-out;

}

@keyframes squish {
  50% {
    scale: 1.4 0.6;
  }
}


.img-slider-dot-btn {
  all: unset;
  cursor: pointer;
  width:  clamp(7px, 1.25vw, 20px);
  height: clamp(7px, 1.25vw, 20px);
  transition: scale 100ms ease-in-out;


}

.img-slider-dot-btn > * {
  stroke: white;
  fill: rgba(37, 37, 39, 0.59);
  height: 100%;
  width: 100%;
  stroke-width: 0.05rem;


}

.img-slider-dot-btn:hover,
.img-slider-dot-btn:focus-visible {
  scale: 1.2;

}
.img-slider-dot-btn:focus {
  outline: none;
}

.img-slider-dot-btn:focus-visible,
.img-slider-btn:focus-visible {
  outline: auto;


}

.skip-link {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

.skip-link:focus-visible {
  top: 0;
  left: 0;
  border: 1px solid black;
  background-color: white;
  padding: .5rem;
  width: auto;
  height: auto;
  margin: 0;
  clip: unset;
  text-decoration: none;
  color: black;
  z-index: 100;
}

.img-slider-media {
  width: 100%;
  height: 100%;
  object-fit: cover;         /* prevents letterboxing */
  display: block;
  background: #000;          /* hides any load gaps */
  /* border-radius optional: usually keep it on frame only */
}

/* Track is the moving strip */
.img-slider-track {
  display: flex;
  height: 100%;
  transition: transform 300ms ease-in-out;
  width: 101%;

}

/* Each slide takes exactly one viewport width */
.img-slider-track > * {
  flex: 0 0 100%;
  height: 100%;
}




