@import "../_globalColor";

/* put the rail on the wrapper in TSX: className="main-div content-rail" */

/* Two-column layout (image slider left, typewriter right) */
.main-div {
  display: grid;
  grid-template-columns:
    clamp(300px, 40vw, 860px)   /* bigger slider column */
    clamp(260px, 25vw, 700px);  /* typewriter */
  align-items: center;
  gap: var(--rail-gap, clamp(24px, 6vw, 96px));
  justify-content: center;      /* center the two tracks inside the rail */
  margin-inline: auto;
}

/* Columns */
.img-container         { justify-self: start; max-width: clamp(320px, 28vw, 560px);   min-width: 0;}
.type-writer-features  { justify-self: start;  max-width: clamp(120px, 35vw, 700px);  min-width: 0; }

/* Base type (desktop) — adjust only sizing, keep your colors */
.type-writer-features {
  font-size: clamp(10px, 1.5vw, 18px);
  line-height: 1.6;
  text-align: justify;
}

.type-writer-features,
.type-writer-features * { overflow-wrap: anywhere; word-break: break-word; hyphens: auto; }

/* ===== Tablet / small laptops (≤1100px) ===== */
@media (max-width: 1100px) {
  .main-div {
    grid-template-columns: minmax(1fr, 1fr) minmax(0, 0.8fr);
    gap: 32px;
  }
}

/* ===== Phones (≤650px) — same as Greeting ===== */
@media (max-width: 650px) {
  .main-div {
    grid-template-columns: 1fr;        /* stack */
    justify-items: start;
    align-items: center;
    gap: 12px;

  }

  .type-writer-features {
    justify-self: start;
    text-align: start;
    max-width: 45ch;                   /* readable lines */
    font-size: clamp(7px, 1.5vw, 10px);
    line-height: 1.5;
  }

  .img-container {
    justify-self: center;
    max-width: 180px;                  /* match other sections’ mobile media cap */
  }
}

