@import "../../_globalColor";

.skills-heading{
  font-weight: bold;
  padding-bottom: 16px;

}

.card-title-project {
  color: $titleColor;
  font-size: clamp(5px, 1.5vw, 25px);
  font-weight: bold;
  margin-top: 5px;
}

.card-subtitle-project {
  color: $cardSubtitle;
  font-size: clamp(5px, 0.95vw, 16px);
  line-height: 0.5rem;
  padding: clamp(1px, 0.95vw, 10px);
}

.project-card-light {
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.project-card-dark {
  box-shadow: 0px 10px 50px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
}

.project-detail {
  text-align: center;
}
.project-card-light:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}
.project-card-dark:hover {
  box-shadow: 0px 20px 100px -10px $darkBoxShadow;
}
.project-image {
  position: relative;
  max-width: 600px;
  height: auto;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}



.project-card-footer span.project-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: clamp(5px, 0.85vw, 15px);
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: clamp(1px, 0.45vw, 1.5px);
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
  font-weight: bold;
}

.features-heading {
  font-size: clamp(10px, 2.5vw, 25px);
  margin-bottom: clamp(10px, 2.5vw, 50px);
}



@media (max-width: 768px) {
  .project-subtitle {
    font-size: 16px;
    text-align: center;
  }
.project-image, .project-card, .project-container {
  height: auto ;
  overflow: visible;
  border-radius: 15px;
}


.extra-image img {
  border-radius: 8px;
}

}
.card-imageproject {
  height: auto;
  width: 100% ;
  max-width: 600px;
  object-fit: contain;
  display: block;
  border-radius: 15px;
}

.projects-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;   // ✅ centers each .project-wrapper horizontally
  padding: 2rem 1rem;
  box-sizing: border-box;
  margin-bottom: clamp(24px, 7vw, 200px);

}








.gameplay-text-div{
  font-size: clamp(5px, 1.5vw, 25px);
  font-weight: bold;
  padding-bottom: clamp(5px, 1.5vw, 25px);
}







  .project-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;   /* equal halves */
    align-items: start;
    gap: clamp(32px, 5vw, 120px); // Responsive horizontal spacing
    margin-bottom: clamp(24px, 5vw, 150px);

  }


.project-video-main, .project-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  box-shadow: 0px 10px 50px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
  width: 100%;
  height: 100%;
  max-width: none;        /* override any earlier caps */
  padding: clamp(12px, 2vw, 60px);  /* keep your nice inner spacing */
}


.project-video-inner {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;   /* height scales with width */
  margin-bottom: 70px;
}

.project-video-inner iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  display: block;
  border: 0;
}

/* If you previously set a cap somewhere else, hard-override it here */
.project-video-main,
.project-video-inner,
.project-video-inner iframe {
  max-width: none !important;
}



.project-video-inner header {
  position: relative;
  transform: translateY(-50%);
  margin-bottom: 40px;
}

.project-card,
.project-video-main { min-width: 0; }

