/* Keep the section centered (you already did this) */
.experience-container {
  max-width: var(--rail-max, 1950px);
  margin-inline: auto;
  padding-inline: var(--rail-pad, clamp(12px, 4vw, 48px));
  margin-bottom: clamp(24px, 5vw, 150px);

}

/* 1) card layout: center cards and cap width */
.experience-cards-div {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 560px)); /* <= cap card width */
  justify-content: center;  /* center the grid within the rail */
  gap: clamp(16px, 3vw, 32px);

}
.experience-heading { padding-bottom: clamp(1px, 2vw, 35px); }
/* 2) readable line length inside cards */
.experience-cards-div p,
.experience-cards-div .desc,
.experience-cards-div .experience-text {
  max-width: 60ch;           /* ~60–70 chars = comfy */
  text-align: left;          /* keep body text left-aligned */
  margin: 0;                 /* let card spacing control vertical rhythm */
  overflow-wrap: anywhere;   /* prevent long tokens from overflowing */
  word-break: break-word;
  hyphens: auto;
}
.experience-cards-div p,
.experience-cards-div .desc,
.experience-cards-div .experience-text-date,
.experience-cards-div .experience-text,
.experience-cards-div .experience-text-role,
.experience-cards-div .dark-mode-text {
  font-size: clamp(9px, 1.55vw, 16px);
}
.experience-cards-div .experience-text-company{
  font-size: clamp(12px, 4vw, 25px);
}
.experience-cards-div .experience-roundedimg {
  width: clamp(70px, 6vw, 100px);
  height: clamp(70px, 6vw, 100px);

}

/* Make sure cards don’t overflow their column */
.experience-cards-div > * {
  width: 100%;
  min-width: 0;
}

/* ===== Tablet / small laptops (≤1100px) ===== */


/* ===== Phones (≤650px) — match your Greeting breakpoints ===== */
@media (max-width: 650px) {
  .experience-cards-div {
    grid-template-columns: 1fr;   /* single column */
    gap: 12px;
    margin: 24px 0;
  }

  /* same tiny mobile font vibe as your site */

}

/* ===== Optional micro-range (≥380px & ≤600px) ===== */
@media (min-width: 380px) and (max-width: 600px) {
  .experience-cards-div { gap: 12px; }
}

