@import "../../_globalColor";

/* The .content-rail from index.css handles width & side padding */

/* Inner grid (same pattern as .greeting-main / background container) */
.skills-main-div {
  display: grid;
  grid-template-columns:
    clamp(420px, 52vw, 700px)    /* image/text order is up to your markup */
    clamp(320px, 38vw, 600px);
  align-items: center;
  gap: var(--rail-gap, clamp(24px, 6vw, 96px));
  justify-content: center;       /* center the two tracks inside the rail */
  margin-inline: auto;
  margin-bottom: clamp(24px, 5vw, 150px);
}

/* Image column */
.skills-image-div {
  justify-self: center;           /* put image on the left by default */
  display: flex;
  align-items: center;
  max-width: clamp(320px, 28vw, 560px);
  overflow: hidden;
}
.skills-image-div img,
.skills-image-div canvas,
.skills-image-div svg {
  width: 100%;
  height: auto;
  display: block;
  max-width: clamp(100px, 30vw, 360px);
}

/* Text column */
.skills-text-div {
  justify-self: start;
  max-width: 700px;
  margin-right: clamp(20px, 30vw, 50px);
}

/* Typography (desktop/base like your other sections) */
.skills-heading { font-weight: 700; font-size: 35px; padding-bottom: 16px; }
.skills-text-subtitle { font-size: 18px; line-height: 1.75; text-align: justify; }
.skill-title { font-size: 20px; font-weight: 600; margin: 12px 0 6px; }
.skill-desc { font-size: 16px; line-height: 1.5; }



/* ===== Phones (match Greeting’s mobile fonts/behavior) ===== */
@media (max-width: 650px) {

  .skills-main-div {
    grid-template-columns:
      minmax(140px, 1fr)   /* text column smaller */
      minmax(100px, 0.8fr); /* image column smaller */
    gap: 8px; /* tighter spacing so they fit */
    align-items: center;
  }

  /* Keep readable line length and left alignment */
  .skills-text-div { justify-self: start; max-width: 200px; text-align: left; }
  .skills-text-subtitle { max-width: 35ch; text-align: left; }

  /* Center image under text on phones */
  .skills-image-div { justify-self: center; max-width: none; margin-top: 8px; }
  .skills-image-div img,
  .skills-image-div canvas,

  /* Mobile font scale — same logic as Greeting (scoped to Skills) */
  .skills-heading { font-size: clamp(10px, 3.8vw, 14px); line-height: 1.2; }
  .skills-text-subtitle { font-size: clamp(10px, 2.5vw, 10px); line-height: 1.5; }
  .skill-title { font-size: clamp(10px, 2.4vw, 10px); }
  .skill-desc  { font-size: clamp(10px, 2.5vw, 10px); line-height: 1.5; }
  .skills-text-div { max-width: 100%; text-align: left; }
}

