@import "../../_globalColor";

/* Rail is provided by .content-rail from index.css */

/* Inner grid (like .greeting-main) */
.background-container-background {
  display: grid;
  grid-template-columns:
    clamp(420px, 52vw, 700px)   /* text */
    clamp(320px, 38vw, 600px);  /* image */
  align-items: center;
  gap: var(--rail-gap, clamp(24px, 6vw, 96px));

  /* center the two-column block inside the rail (same as Greeting) */
  justify-content: center;
  margin-inline: auto;
  margin-bottom: clamp(24px, 5vw, 150px);
}

/* Text */
.background-text { justify-self: start; max-width: 700px; }
.skills-heading { font-weight: 700; font-size: 35px; padding-bottom: 16px; }
.background-description { font-size: 18px; line-height: 1.75; text-align: justify; }

/* Image (keep from overflowing) */
.skills-image {
  justify-self: end;
  display: flex;
  align-items: center;
  max-width: clamp(320px, 28vw, 560px);
  overflow: hidden;
}
.skills-image img,
.skills-image canvas,
.skills-image svg {
  width: 100%;
  height: auto;
  display: block;
}

/* ===== Tablets general tweaks ===== */
@media (max-width: 992px) {
  .skills-heading { font-size: 20px; }         /* tablet size */
  .background-description { font-size: 16px; }
}

/* ===== Mobile font scale (match Greeting logic) ===== */
@media (max-width: 650px) {
  /* Smaller headings & body text, but scoped to Background section */
  .skills-heading { font-size: clamp(10px, 3.8vw, 14px); line-height: 1.2; }
  .background-text h2 { font-size: clamp(10px, 2.4vw, 10px); }
  .background-text h3 { font-size: clamp(10px, 2vw, 10px); }
  .background-description { font-size: clamp(10px, 2.5vw, 10px); line-height: 1.5; }
}

/* ===== Layout: keep two columns down to ~560px, then stack ===== */
@media (min-width: 560px) and (max-width: 1100px) {
  .background-container-background {
    grid-template-columns: minmax(0, 1fr) minmax(0, 0.8fr);
    gap: 16px; /* tighter so it fits */
  }
  .skills-image { max-width: clamp(180px, 35vw, 360px); }
}

/* Keep two columns even below 560px */
@media (max-width: 559px) {
  .background-container-background {
    /* two fluid tracks that can shrink */
    grid-template-columns: minmax(0, 1fr) minmax(120px, 35vw);
    gap: 8px;
    margin: 16px 0;
    /* remove the hard cap — the rail already handles padding */
    max-width: none;
    justify-items: stretch;
    align-items: center;
  }

  .background-text {
    max-width: none;   /* let text take the remaining room */
  }

  .skills-image {
    justify-self: end;                       /* keep it to the right */
    max-width: clamp(120px, 35vw, 180px);    /* small but readable */
    margin-top: 0;
  }

  .skills-image img,
  .skills-image canvas,
  .skills-image svg {
    width: 100%;
    height: auto;
    display: block;
  }

  /* your mobile font clamp (same as greeting) */
  .skills-heading { font-size: clamp(10px, 3.8vw, 14px); line-height: 1.2; }
  .background-description { font-size: clamp(10px, 2.5vw, 10px); line-height: 1.5; max-width: 35ch; }
}


