@import "../../_globalColor";
.main-button {
  background-color: $buttonColor;
  border: solid 1px $buttonColor;
  color: $textColorDark;
  font-weight: 700;
  width: max-content;
  padding: 10px 15px;
  margin-right: 50px;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;

  display: inline-flex;      /* changed from block → inline-flex */
  align-items: center;       /* vertical alignment for text + icon */
  gap: 8px;                  /* space between text and icon */
  letter-spacing: 2px;
  margin-top: 20px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}

.main-button:hover {
  background-color: $darkBoxShadow2;
  color: $buttonColor;
  transition: all 0.3s ease 0s;
  transform: translateY(-3px);
}

.project-button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.project-button > .main-button {
  margin-right: 0 !important;
}

/* ===== Small laptops / narrow desktops (993–1200px) ===== */
@media (max-width: 1200px) and (min-width: 993px) {
  .main-button { font-size: 0.9375rem; padding: 0.6em 1em; }
  .main-button svg { width: 1.2em; height: 1.2em; }
}

/* ===== Tablets (769–992px) ===== */
@media (max-width: 992px) and (min-width: 769px) {
  .main-button { font-size: 0.875rem; padding: 0.58em 0.95em; }
  .main-button svg { width: 1.15em; height: 1.15em; }
}

/* ===== Phones (≤768px) — your rule, with tiny tweaks ===== */
@media (max-width: 768px) {
  .main-button {
    font-size: 0.428rem;           /* your value */
    padding: 0.5em 0.7em;          /* proportional to font-size */
    margin-right: 90px;            /* if possible, prefer container gap instead */
    margin-top: 10px;
  }
  .main-button svg {
    width: 1.5em;                   /* tied to text size */
    height: 1.5em;
  }
}

/* ===== Very small phones (≤360px) — optional safety net ===== */
@media (max-width: 360px) {
  .main-button { font-size: 0.5rem; padding: 0.45em 0.65em; }
  .main-button svg { width: 1.4em; height: 1.4em; }
}
