/* ===== Global reset/sizing ===== */
*, *::before, *::after { box-sizing: border-box; }
html, body { margin: 0; padding: 0; width: 100%; overflow-x: hidden; scroll-behavior: smooth; }

/* ===== Fonts (use the actual file format!) ===== */
@font-face {
  font-family: "Pixelmania";
  font-style: normal;
  font-weight: 400;
  src: local("Pixelmania"),
  url("./assets/fonts/Pixelmania.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Pixelcraft";
  font-style: normal;
  font-weight: 400;
  src: local("Pixelcraft"),
  url("./assets/fonts/Pixelcraft.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"),
  url("./assets/fonts/Montserrat-Regular.ttf") format("truetype");
  font-display: swap;
}

/* ===== Type scale (mobile-first) ===== */
:root {
  /* Adjust 1rem if you want larger/smaller base */
  font-size: 16px;
}
body {
  font-family: Montserrat, "Montserrat", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  font-size: clamp(15px, 2.7vw, 19px);
  line-height: 1.5;                 /* unitless → scales better */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 { font-size: clamp(24px, 6vw, 40px); margin: 0.6em 0 0.4em; }
h2 { font-size: clamp(20px, 5vw, 32px); margin: 0.6em 0 0.4em; }
h3 { font-size: clamp(18px, 4.5vw, 28px); margin: 0.6em 0 0.4em; }
p  { margin: 0 0 1em; }

/* ===== Fluid media ===== */
img, video, iframe { max-width: 100%; height: auto; display: block; }
.responsive-video,
.fluid-media { width: 100%; }
.responsive-video iframe,
.fluid-media iframe { width: 100%; aspect-ratio: 16 / 9; height: auto; border: 0; }

/* ===== Layout helpers ===== */
.container { width: min(1100px, 100% - 32px); margin: 0 auto; }
.stack-sm > * + * { margin-top: 8px; }
.stack-md > * + * { margin-top: 16px; }
.stack-lg > * + * { margin-top: 24px; }

/* ===== Breakpoints ===== */
/* Tablet */
@media (min-width: 601px) and (max-width: 992px) {
  :root { font-size: 16px; }
}

/* Desktop */
@media (min-width: 993px) {
  :root { font-size: 16px; }
}

/* ===== Shared content rail (align all sections) ===== */
:root{
  --rail-max: 1950px;                   /* unified content width up to 4K */
  --rail-pad: clamp(12px, 4vw, 48px);   /* consistent side padding */
  --rail-gap: clamp(24px, 6vw, 96px);   /* optional inner gap helper */
}

/* Use this on any section wrapper to align with the same rail */
.content-rail{
  max-width: var(--rail-max);
  margin-inline: auto;
  padding-inline: var(--rail-pad);
}

/* Match your phone padding exactly */
@media (max-width: 600px){
  .content-rail{
    padding-inline: 16px;
  }
}

