@import "../../_globalColor";

/* Cards grid – centered and capped like Experience/Projects */
.education-card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 560px)); /* cap card width */
  justify-content: center;  /* center the grid inside the rail */
  gap: clamp(16px, 3vw, 32px);
}

/* Keep headings consistent with your other sections */
.education-heading {
  /* desktop size is already set inline in JS; nothing else needed here */
  margin-bottom:clamp(10px, 2.5vw, 100px);
}

/* Ensure cards don’t overflow their column */
.education-card-container > * {
  width: 100%;
  min-width: 0;
}

/* Nice readable line lengths inside card bodies */
.education-card-container p,
.education-card-container .edu-desc,
.education-card-container .education-text {
  max-width: 60ch;
  text-align: left;
  margin: 0;
  overflow-wrap: anywhere;
  word-break: break-word;
  hyphens: auto;
}
.education-card-container .education-roundedimg{
  width: clamp(50px, 6vw, 100px);
  height: clamp(50px, 6vw, 100px);
}

.education-card-container .education-text-school{
  font-size: clamp(12px, 1.85vw, 25px);
}
.education-card-container .education-text-subHeader{
  font-size: clamp(8px, 1.55vw, 18px);
}
.education-card-container .education-text-duration{
  font-size: clamp(8px, 1.25vw, 16px);
}
.education-card-container .education-text-desc{
  font-size: clamp(8px, 1.25vw, 16px);
}
.education-card-container .education-text-bullets{
  li {
    font-size: clamp(8px, 1.25vw, 14px);

  }
}
/* ===== Tablet / small laptops (≤1100px) ===== */
@media (max-width: 1100px) {
  .education-card-container {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 24px;
  }
}

/* ===== Phones (≤650px) — same as Greeting ===== */
@media (max-width: 650px) {
  .education-section {
    padding-inline: 16px;    /* matches your mobile padding */
    margin: 24px 0;
  }

  .education-card-container {
    grid-template-columns: 1fr;  /* single column */
    gap: 12px;
  }

  /* Mobile font clamps – match Greeting’s vibe (scoped to Education) */
  .education-heading {
    font-size: clamp(10px, 3.8vw, 14px);
    line-height: 1.2;
    padding-bottom: 10px;
  }

  .education-card-container p,
  .education-card-container .edu-desc,
  .education-card-container .education-text {
    font-size: clamp(10px, 2.5vw, 10px);
    line-height: 1.5;
    max-width: 35ch;         /* comfy mobile line length */
  }
}

/* ===== Optional micro-range (≥380px & ≤600px) ===== */
@media (min-width: 380px) and (max-width: 600px) {
  .education-card-container { gap: 12px; }
}

/* ===== 4K / ultrawide (≥2560px) — no font blow-up ===== */
@media (min-width: 2560px) {
  .education-card-container {
    gap: clamp(24px, 2.5vw, 48px);
  }
}
