.splash-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.splash-animation-container {
  width: 700px;
  height: 600px;
  max-width: 80vw;
  max-height: 40vh;
}

.splash-title-container {
  font-size: 2.5rem;
  text-decoration: none;

}
.splash-title {
  font-family: "Pixelcraft", sans-serif;
  font-weight: bold;
  font-variant-ligatures: no-common-ligatures;
  -webkit-font-variant-ligatures: no-common-ligatures;
  letter-spacing: 13px;
  font-size: clamp(10px, 3.5vw, 50px);
  padding-left: 50px;

}
