@import "../../_globalColor";

/* ===== Greeting Section ===== */

.greeting-main {
  display: grid;
  grid-template-columns:
    clamp(420px, 52vw, 700px)
    clamp(320px, 38vw, 600px);
  align-items: center;
  gap: clamp(24px, 6vw, 64px);

  margin-inline: auto;
  justify-content: center; /* NEW */
  margin-bottom: clamp(24px, 5vw, 150px);

}


.greeting-main {
  display: grid;
  grid-template-columns:
    clamp(320px, 52vw, 700px)
    clamp(250px, 38vw, 600px);
  align-items: center;
  gap: clamp(24px, 6vw, 64px);
}

.greeting-text-div {
  max-width: 700px;
  justify-self: start;
}

.greeting-image-div {
  justify-self: center;
  display: flex;
  align-items: center;
}

.greeting-image {
  width: 100%;
  max-width: clamp(150px, 20vw, 320px);
  border-radius: 10px;
  display: block;


}

.button-greeting-div {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 16px;
}


/* Phone (stacked layout) */
@media (max-width: 650px) {
  .greet-main {
    padding-inline: 16px;
    margin: 35px 0;

  }

  .greeting-main {
    row-gap: 16px;        /* extra breathing room without changing layout */

  }

  .greeting-text-div {
    order: 1;
    justify-self: center;
    text-align: left;          /* change to center if you prefer */
    max-width: 35ch;           /* readable line length */
  }
  .greeting-text-div p {
    margin-bottom: 8px;   /* subtle spacing, keeps your font sizes */
    line-height: 1.35;    /* looks smaller without changing font-size */
  }
  .greeting-image-div {
    order: 1;
    justify-self: center;
    max-width: 90px;          /* smaller image for narrow phones */
    margin-bottom: 6.5rem;
  }

  .greeting-image {
    max-width: 180px;          /* smaller image for narrow phones */
    border-radius: 10px;
  }

  .button-greeting-div {
    justify-content: center;
    gap: 5px;
    margin-top: 5px;

  }

  /* Smaller headings for narrow screens */
  h1 { font-size: clamp(10px, 3.8vw, 14px); }
  h2 { font-size: clamp(10px, 2.4vw, 10px); }
  h3 { font-size: clamp(10px, 2vw, 10px); }
  body { font-size: clamp(10px, 2.5vw, 10px); }

}

.greeting-text{
  font-size: clamp(12px, 2vw, 30px);
}
/* Optional: keep two-column on wider phones (≥380px) */
@media (max-width: 600px) {
  .greeting-main {
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    align-items: center;
  }

  .greeting-text-div { justify-self: start; }
  .greeting-image-div { justify-self: center; }

  .greeting-image {
    max-width: 140px;
  }
}
