@import "../../_globalColor";

.social-media-div {
  font-size: 2em;
}

.icon-button {
  margin: 0 0px 10px 8px; // top, right, bottom, left
  display: inline-block;  // ensures they line up horizontally
}

.icon-button i {
  color: $textColorDark;
  border-radius: 2.6rem;
  cursor: pointer;
  display: inline-block;
  font-size: 1.3rem;
  height: 2.6rem;
  line-height: 2.6rem;
  margin: 0 2px;
  position: relative;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 2.6rem;
  margin-bottom: 0px;
  transition: 0.2s ease-in;
}

.facebook i {
  background-color: $faceBook;
}

.linkedin i {
  background-color: $linkedin;
}

.github i {
  background-color: $github;
}

.gitlab i {
  background-color: $gitlab;
}

.google i {
  background-color: $google;
}

.twitter i {
  background-color: $twitter;
}

.medium i {
  background-color: $medium;
}

.stack-overflow i {
  background-color: $stackoverflow;
}

.kaggle i {
  background-color: $kaggle;
}

.instagram i {
  background-color: $instagram;
}

/* Hover Transition */
.twitter i:hover,
.google i:hover,
.gitlab i:hover,
.github i:hover,
.linkedin i:hover,
.facebook i:hover,
.instagram i:hover,
.stack-overflow i:hover,
.kaggle i:hover,
.medium i:hover {
  background-color: $textColor;
}


/* Phone */
@media (max-width: 600px) {
  .social-media-div {
    gap: 4px;
    justify-content: center;
  }
   .icon-button {
    width: 18px;
    height: 18px;
  }

  .icon-button i {
    font-size: 10px;
    height: 1.2rem;
    width: 1.2rem;
    margin: 0 5px;
    position: relative;
    text-align: center;
    line-height: 2;

  }
}

/* ===== Tablet ===== */
@media (min-width: 601px) and (max-width: 992px) {
  .social-media-div {
    gap: 6px;
    justify-content: flex-start; /* or center if you prefer */
  }

  .icon-button {
    width: 28px;
    height: 28px;
  }

  .icon-button i {
    font-size: 14px;
    height: 1.8rem;
    width: 1.8rem;
    margin: 0 6px;
    line-height: 1.8rem;
  }
}

/* ===== Small laptops / narrow desktops ===== */
@media (min-width: 993px) and (max-width: 1200px) {
  .social-media-div {
    gap: 8px;
  }

  .icon-button {
    width: 36px;
    height: 36px;
  }

  .icon-button i {
    font-size: 16px;
    height: 2rem;
    width: 2rem;
    margin: 0 6px;
    line-height: 2rem;
  }
}

