@import "../../_globalColor";

/* =========================
   Header (matches rail logic)
   ========================= */

/* The header itself sits on the shared content rail */

.headroom-wrp {
  margin-bottom: clamp(24px, 5vw, 60px);
}


.header {
  /* align to the same rail as sections */
  max-width: var(--rail-max);
  margin-inline: auto;
  padding-inline: var(--rail-pad);
  padding-block: clamp(10px, 1.2vw, 18px);

  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: clamp(12px, 2vw, 24px);

  /* don’t set colors here; keep your existing theme classes working */
}

/* Logo */
.logo {
  display: inline-flex;
  align-items: center;
  text-decoration: none; /* keep your color from existing theme */
}
.logo-name {
  /* keep your font families; just ensure it scales a bit on small screens */
  font-size: clamp(10px, 1.75vw, 22px);
  line-height: 1.1;
}

/* Menu (desktop) */
.menu {
  display: flex;
  align-items: center;
  gap: clamp(12px, 2vw, 24px);
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu li a {
  text-decoration: none; /* colors come from your theme */
  padding: 6px 8px;
  line-height: 1.2;
}

/* Checkbox + burger icon */
.menu-btn {
  display: none; /* hidden on desktop; used for mobile toggle */
}

/* Burger icon base (structure already in your JSX) */
.menu-icon {
  display: none; /* shown on mobile */
  cursor: pointer;
  user-select: none;
}
.navicon {
  position: relative;
  display: inline-block;
  width: 28px;
  height: 2px;
  background: currentColor;     /* inherits from your theme */
  transition: transform 200ms ease;
}
.navicon::before,
.navicon::after {
  content: "";
  position: absolute;
  left: 0;
  width: 28px;
  height: 2px;
  background: currentColor;
  transition: transform 200ms ease, top 200ms ease, opacity 200ms ease;
}
.navicon::before { top: -8px; }
.navicon::after  { top:  8px; }

/* =========================
   Breakpoints (same vibes)
   ========================= */

/* Small laptops / tablets: keep header on the rail; reduce spacing a touch */
@media (max-width: 1100px) {
  .header { gap: 16px; }
  .menu { gap: 16px; }

}

/* Phones (≤650px): same “phone padding 16px” rule you used on sections */
@media (max-width: 650px) {
  .header {
    padding-inline: 16px;         /* same as your sections on mobile */
    gap: 12px;
  }

  /* Show burger, hide inline menu */
  .menu-icon { display: inline-flex; align-items: center; }
  .menu {
    display: none;                /* hidden until toggled */
    flex-direction: column;
    gap: 8px;
    margin-top: 10px;
    width: 100%;
  }

  /* When the checkbox is checked, reveal the menu */
  .menu-btn:checked ~ .menu {
    display: flex;
  }

  /* Animate burger to X when opened */
  .menu-btn:checked ~ .menu-icon .navicon {
    transform: rotate(45deg);
  }
  .menu-btn:checked ~ .menu-icon .navicon::before {
    top: 0;
    transform: rotate(90deg);
  }
  .menu-btn:checked ~ .menu-icon .navicon::after {
    top: 0;
    opacity: 0;
  }

  /* Slightly smaller nav text on phones (match your small type scale vibe) */
  .menu li a {
    font-size: clamp(10px, 2.5vw, 12px);
    padding: 8px 4px;
  }

  .logo-name {
    font-size: clamp(9px, 1.4vw, 12px);
  }
}

/* 4K polish: keep header comfy but not huge */
@media (min-width: 2560px) {
  .header { gap: clamp(16px, 1.2vw, 28px); }
  .menu { gap: clamp(16px, 1.2vw, 28px); }
  .logo-name { font-size: clamp(16px, 1.4vw, 24px); }
}
.logo-name {
  font-family: "Pixelmania", Montserrat, system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  /* keep your current size from the new header; this just sets the font face */
}
